.swiper-tab {
  position: relative;
  .swiper-container {
    width: 100%;
    overflow: hidden;
    border-bottom: 6px solid #FF7700;
    .swiper-wrapper {
      transition: all 300ms ease 0s;
      display: flex;
      .swiper-slide{
        cursor: pointer;
        padding: 0px 10px;
        flex-shrink: 0;
        width: 20%;
        height: 34px;
        line-height: 34px;
        .main {
          background: #FFFFFF;
          border-radius: 4px 4px 0px 0px;
          font-size: 14px;
          color: #333333;
          text-align: center;
          height: 100%;
          a {
            display: block;
          }
        }
        &.active {
          .main {
            background: #FF7700;
            color: #fff;
            a {
              color: inherit;
            }
          }
        }
      }
    }
  }
  .swiper-button-prev, .swiper-button-next {
    position: absolute;
    display: inline-block;
    width: 30px;
    height: 30px;
    background: #FF7700;
    border-radius: 50%;
    text-align: center;
    line-height: 30px;
    color: #fff;
    top: 5px;
    cursor: pointer;
  }
  .swiper-button-prev {
    left: -50px;
  }
  .swiper-button-next {
    right: -50px;
  }
}